export class SettingModel {
  approveAfterSubscribe : boolean;
  subscribeByUser : boolean;
  month_startegy : string;
  discount_startegy : string;
  newSubscriptionNotification:boolean;
  newSubscriptionSendStaff:boolean;
  newSubscriptionSendEmail:boolean;
  newSubscriptionRoles:any;

  membershipNotification:boolean;
  membershipSendStaff:boolean;
  membershipSendEmail:boolean;
  membershipRoles:any;

  checkExpirationColum:string;
  checkExpirationDays:string;
  checkSchedualColum:string;
  checkSchedualDays:string;
  checkPaymentColum:string;
  checkPaymentDays:string;



  clear() {
    this.approveAfterSubscribe = false;
    this.subscribeByUser = false;

    this.newSubscriptionNotification = false;
    this.newSubscriptionSendStaff=false;
    this.newSubscriptionSendEmail=false;
    this.newSubscriptionRoles='';

    this.membershipNotification = false;
    this.membershipSendStaff=false;
    this.membershipSendEmail=false;
    this.membershipRoles ='';
    this.month_startegy = '';
    this.discount_startegy = '';

    this.checkExpirationDays ='';
    this.checkExpirationColum ='';
    this.checkSchedualColum ='';
    this.checkSchedualDays ='';
    this.checkPaymentColum ='';
    this.checkPaymentDays ='';

  }
}
